import React from 'react'

import styles from './IndustrySolutions.module.scss'
import topImg from '../../img/industry-solutions/industrySolutionsTopImg.png'
import topImg2 from '../../img/industry-solutions/industrySolutionsTopImg2.png'
import housingImg from '../../img/industry-solutions/housing-services.png'
import mfoImg from '../../img/industry-solutions/MFO.png'
import crowdfunding from '../../img/industry-solutions/crowdfunding.png'
import internetShopImg from '../../img/industry-solutions/internet-shop.png'
import onlineStudyImg from '../../img/industry-solutions/online-study.png'
import TopBlockWithImageNEW from '../../components/Blocks/TopBlockWithImageNEW/TopBlockWithImageNEW'
import ColouredBGBlock from '../../components/Blocks/ColouredBGBlock/ColouredBGBlock'
import MaxWidthBlock from '../../components/Blocks/MaxWidthBlock/MaxWidthBlock'
import BlackCard from '../../components/Cards/BlackCard/BlackCard'

const IndustrySolutions = ({history}) => {

  const solutionsList = [
    {
      header: '',
      image: mfoImg,
      buttonText: '',
      children: (
        <p>
              ,   ,    . , SDK,  .
        </p>
      ),
      clickHandler: () => {
        history.push("/industry-solutions/mfo")
      }
    },
    {
      header: '',
      image: crowdfunding,
      buttonText: '',
      children: (
        <p>
       02.08.2019 259-,   ,    ,   API.        </p>
      ),
      clickHandler: () => {
        history.push("/industry-solutions/crowd-funding")
      }
    },
    {
      header: '-',
      image: internetShopImg,
      buttonText: '',
      children: (
        <p>
 ,  ,  ,   ,  ,    QR-    .        </p>
      ),
      clickHandler: () => {
        history.push("/industry-solutions/internet-shops")
      }
    },
    {
      header: '-',
      image: onlineStudyImg,
      buttonText: '',
      children: (
        <p>
    ,   ,   qr-   .        </p>
      ),
      clickHandler: () => {
        history.push("/industry-solutions/online-study")
      }
    },
    {
      header: '',
      image: housingImg,
      buttonText: '',
      children: (
        <p>
 ,  ,  ,     .        </p>
      ),
      clickHandler: () => {
        history.push("/industry-solutions/housing")
      }
    },
    
 
  ]

    return (
        <div className={styles.IndustrySolutions}>
            <TopBlockWithImageNEW
              topBlockImg={topImg}
              topBlockImgNarrow={topImg2}
              topBlockItemText1={'     '}
              topBlockItemText2={'       .'}
            />

            <ColouredBGBlock>
              <MaxWidthBlock>
                <div className={styles.solutionsBlock}>
                  {
                    solutionsList.map(item => <BlackCard {...item} key={item.header}/>)
                  }
                </div>
              </MaxWidthBlock>
            </ColouredBGBlock>
        </div>
    )
}

export default IndustrySolutions