import React from 'react'
import DropDown from '../UI/DropDown/DropDown'
import NavLinkPurple from '../UI/NavLinkPurple/NavLinkPurple'

import styles from './NavMenu.module.scss'

const NavMenu = ({ isHeader }) => {
   
   const productsList = [
    { title: ' ', link: '/receiving-payments' },
    { title: '', link: '/payouts' },
    { title: ' ', link: '/industry-solutions' },
    { title: '-  ', link: '/credits' },
    { title: '', link: '/developers/connection' },
   ] 

    return (
        <ul className={isHeader ? styles.header : styles.sidebar}>
            <li className={styles.item}>
                <DropDown
                    links={productsList}
                    title={''}
                    titleClass={styles.link}
                    isHeader={isHeader}
                />
            </li>
            <li className={styles.item}>
                <DropDown
                    links={productsList}
                    title={' '}
                    titleClass={styles.link}
                    isHeader={isHeader}
                />
            </li>
 
            <li className={styles.item}>
                <NavLinkPurple to="/developers" className={styles.link}></NavLinkPurple>
            </li>
       

        </ul>

    )
}

export default NavMenu