import advantagesIcon1 from '../../../img/online-study/advantagesIcon1.svg'
import advantagesIcon2 from '../../../img/online-study/advantagesIcon2.svg'
import advantagesIcon3 from '../../../img/online-study/advantagesIcon3.svg'
import advantagesIcon4 from '../../../img/online-study/advantagesIcon4.svg'
import advantagesIcon5 from '../../../img/online-study/advantagesIcon5.svg'
import advantagesIcon6 from '../../../img/online-study/advantagesIcon6.svg'
import advantagesIcon7 from '../../../img/online-study/advantagesIcon7.svg'
import advantagesIcon8 from '../../../img/online-study/advantagesIcon8.svg'
