import possibilitiesIcon1 from '../../../img/online-study/possibilitiesIcon1.svg'
import possibilitiesIcon2 from '../../../img/online-study/possibilitiesIcon2.svg'
import possibilitiesIcon3 from '../../../img/online-study/possibilitiesIcon3.svg'
import possibilitiesIcon4 from '../../../img/online-study/possibilitiesIcon4.svg'
import possibilitiesIcon5 from '../../../img/online-study/possibilitiesIcon5.svg'
import possibilitiesIcon6 from '../../../img/online-study/possibilitiesIcon6.svg'
import possibilitiesIcon7 from '../../../img/online-study/possibilitiesIcon7.svg'
