import React from 'react'
import Socials from '../Socials/Socials'
import NavLinkPurple from '../UI/NavLinkPurple/NavLinkPurple'

import styles from './Footer.module.scss'
import visaMaster from '../../img/footer/visa-master.svg'

const Footer = (props) => {

    return (
        <div className={styles.Footer}>
            <div className={styles.footerNavbar}>
                <ul  className={styles.list}>
                    <li className={styles.item}>
                        <NavLinkPurple to="/contacts" className={styles.link}>  </NavLinkPurple>
                    </li>
                    <li className={styles.item}>
                        <NavLinkPurple to="/partners" className={styles.link}></NavLinkPurple>
                    </li>
                    <li className={styles.item}>
                        <NavLinkPurple to="/system-rules" className={styles.link}> </NavLinkPurple>
                    </li>
                    <li className={styles.item}>
                        <NavLinkPurple to="/help" className={styles.link}> </NavLinkPurple>
                    </li>
                </ul>
                <Socials/>
            </div>
            <hr/>
            <div className={styles.footerNavbar2}>
                <ul  className={styles.list}>
                    <li className={styles.item}>
                        <NavLinkPurple to="/about-us" className={styles.link}> </NavLinkPurple>
                    </li>
                    <li className={styles.item}>
                        <NavLinkPurple to="/developers" className={styles.link}></NavLinkPurple>
                    </li>
                    <li className={styles.item}>
                        <NavLinkPurple to="/vacancies" className={styles.link}></NavLinkPurple>
                    </li>
                </ul>
                <div className={styles.visaMasterBlock}>
                    <div>
                        <span> 19992020</span>
                    </div>
                    <div>
                        <img src={visaMaster} alt="visaMaster"/>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Footer