import React from 'react'

import styles from './MFO.module.scss'
import mfoTopImg1 from '../../../img/mfo/mfoTopImg1.png'
import mfoTopImg2 from '../../../img/mfo/mfoTopImg2.png'
import mfoServicesImg1 from '../../../img/mfo/mfoServicesImg1.svg'
import mfoServicesImg2 from '../../../img/mfo/mfoServicesImg2.svg'
import mfoServicesImg3 from '../../../img/mfo/mfoServicesImg3.svg'
import mfoServicesImg4 from '../../../img/mfo/mfoServicesImg4.svg'
import mfoServicesImg5 from '../../../img/mfo/mfoServicesImg5.svg'
import TopBlockWithImageNEW from '../../../components/Blocks/TopBlockWithImageNEW/TopBlockWithImageNEW'
import ColouredBGBlock from '../../../components/Blocks/ColouredBGBlock/ColouredBGBlock'
import MaxWidthBlock from '../../../components/Blocks/MaxWidthBlock/MaxWidthBlock'
import SolutionsCard from '../../../components/Cards/SolutionsCard/SolutionsCard'

const MFO = (props) => {

    const mfoServicesList = [
        {
            header: '',
            image: mfoServicesImg1,
            text: '        ,      .             .'
        },
        {
            header: '  ',
            image: mfoServicesImg2,
            text: '    ,   .         SMS,          .         .'
        },
        {
            header: ' ',
            image: mfoServicesImg3,
            text: '      .      .'
        },
        {
            header: '  ',
            image: mfoServicesImg5,
            text: ' SDK             QR-.'
        },
        {
            header: '  ',
            image: mfoServicesImg4,
            text: '          AFT.            .      AFT , VEPAY    ,    .'
        },
    ]

    return (
        <div className={styles.MFO}>
            <TopBlockWithImageNEW
                topBlockImg={mfoTopImg1}
                topBlockImgNarrow={mfoTopImg2}
                centeredBlockStyle={ styles.centeredBlockStyle}
                topBlockItemText1='  '
                topBlockItemText2='            .     ,   ,    .'
            />

            <ColouredBGBlock>
                <MaxWidthBlock>
                    <div className={styles.mfoServicesBlock}>
                        {
                            mfoServicesList.map(item => <SolutionsCard {...item} key={item.header} />)
                        }
                    </div>
                </MaxWidthBlock>
            </ColouredBGBlock>
        </div>
    )
}

export default MFO