import React, { useEffect, useRef, useState } from 'react'

import styles from './ReceivingPayments.module.scss'
import SolutionsCard from '../../components/Cards/SolutionsCard/SolutionsCard'
import topImg from '../../img/receiving-payments/paymentReceivingTopImg.png'
import topImgNarrow from '../../img/receiving-payments/paymentReceivingTopImg2.png'
import solutionImg1 from '../../img/receiving-payments/method1.svg'
import solutionImg2 from '../../img/receiving-payments/method2.svg'
import solutionImg3 from '../../img/receiving-payments/method3.svg'
import solutionImg4 from '../../img/receiving-payments/method4.svg'
import solutionImg5 from '../../img/receiving-payments/method5.svg'
import solutionImg6 from '../../img/receiving-payments/method6.svg'
import solutionImg7 from '../../img/receiving-payments/method7.svg'
import solutionImg8 from '../../img/receiving-payments/method8.svg'
import solutionImg9 from '../../img/receiving-payments/method9.svg'
import widgetImg1 from '../../img/receiving-payments/widgetImg1.png'
import widgetImg2 from '../../img/receiving-payments/widgetImg2.png'
import widgetImg3 from '../../img/receiving-payments/widgetImg3.png'
import widgetImg4 from '../../img/receiving-payments/widgetImg4.png'
import TopBlockWithImageNEW from '../../components/Blocks/TopBlockWithImageNEW/TopBlockWithImageNEW'
import ColouredBGBlock from '../../components/Blocks/ColouredBGBlock/ColouredBGBlock'
import MaxWidthBlock from '../../components/Blocks/MaxWidthBlock/MaxWidthBlock'
import TextWithCircle from '../../components/UI/TextWithCircle/TextWithCircle'
import HowToPayStep from '../../components/HowToPayStep/HowToPayStep'

const ReceivingPayments = (props) => {
    const [stepNumber, setStepNumber] = useState(0)
    const [fadeIn, setFadeIn] = useState(false)
    const imageRef = useRef()

    useEffect(() => {
        setFadeIn(true)
        setTimeout(() => {
            setFadeIn(false)
        }, 1000)
    }, [stepNumber])

    const widgetImgCls = [
        styles.widgetImg,
        fadeIn? styles.fadeIn: ''

    ]

    const tickedTextList1 = [
        '    3     ; ',
        '   - ; ',
        ' Anti-fraud ,   ;',
       
    ]

    const tickedTextList2 = [
        'VEPAY    PCI DSS;',
        '       ; ',
        '  24/7',
    ]

    const solutionsList = [
        {
            header: '  ',
            text: '      .       .',
            image: solutionImg1
        },
        {
            header: 'QR-',
            text: ' / /  /   QR-.   QR-,           ,   .',
            image: solutionImg2
        },
        {
            header: ' ',
            text: '      .     -.         .',
            image: solutionImg3
        },
        {
            header: 'WeChat',
            text: '       .        .   WeChat    QR-.',
            image: solutionImg4
        },
        {
            header: ' ',
            text: '        .        .',
            image: solutionImg5
        },
        {
            header: '  ',
            text: '      .      .     .',
            image: solutionImg6
        },
        {
            header: ' ',
            text: '    (Mastercard, Visa, ).      PCI DSS level 1.',
            image: solutionImg7
        },
        {
            header: '',
            text: '      .               .',
            image: solutionImg8
        },
        {
            header: '  ',
            text: '  ,     PIN-     .',
            image: solutionImg9
        },
    ]
    const steps = [
        {
            text: '    ,         .'
        },
        {
            text: '    ,    ,   ""'
        },
        {
            text: '    -   .     sms,   ""'
        },
        {
            text: '     .'
        },
    ]
    const stepImages = [
        widgetImg1,
        widgetImg2,
        widgetImg3,
        widgetImg4
    ]

    return (
        <div className={styles.ReceivingPayments}>
            <TopBlockWithImageNEW
                topBlockImg={topImg}
                topBlockImgNarrow={topImgNarrow}
                topBlockItemText1={'    '}
                topBlockItemText2={'  ,      .   :  -,   .   .  :  ,        .'}
            />

            <ColouredBGBlock>
                <MaxWidthBlock>

                   <center> <h1> VEPAY:</h1></center>

                    <div className={styles.tickedBlocks}>
                        <div className={styles.tickedBlock1}>
                            {
                                tickedTextList1.map(text => <TextWithCircle className={styles.tickedText} text={text} type={'violet'} key={text}/>)
                            }
                        </div>
                        <div className={styles.tickedBlock2}>
                            {
                                tickedTextList2.map(text => <TextWithCircle className={styles.tickedText} text={text} type={'violet'} key={text}/>)
                            }
                        </div>
                    </div>

                    

                </MaxWidthBlock>
                <div className={styles.solutionsWrap}>
                    <h1 className={styles.solutionsHeader}> </h1>
                    <div className={styles.solutionsBlock}>
                        {
                            solutionsList.map((item, i) => <SolutionsCard {...item} key={i}/>)
                        }
                    </div>
                </div>

                <div className={styles.howToPayBlock}>
                    <center><h1>   VEPAY</h1></center>

                    <div className={styles.howToPayBlockContent}>
                        <div className={styles.contentItem}>
                            <img ref={imageRef} className={widgetImgCls.join(' ')} src={stepImages[stepNumber]} alt="widgetImg"/>
                        </div>
                        <div className={styles.contentItem}>
                            {
                                steps.map((item, i) => {
                                    return (
                                        <HowToPayStep
                                            key={i}
                                            {...item}
                                            number={i + 1}
                                            active={i === stepNumber}
                                            clickHander={() => {
                                                setStepNumber(i)
                                                if (document.body.clientWidth < 900) {
                                                    imageRef.current.scrollIntoView({
                                                        behavior: "smooth",
                                                        });
                                                }
                                            }}
                                        />
                                    )
                                })
                            }
                        </div>
                    </div>
                </div>
            </ColouredBGBlock>

            


            
        </div>
    )
}

export default ReceivingPayments