import React from 'react'
import { NavLink } from 'react-router-dom'

import styles from './LegalEntity.module.scss'

import productImg1 from '../../img/legal-entity-main/productsImg1.png'
import productImg2 from '../../img/legal-entity-main/productsImg2.png'
import productImg3 from '../../img/legal-entity-main/productsImg3.png'
import productImg4 from '../../img/legal-entity-main/productsImg4.png'
import paymentsVariants1 from '../../img/legal-entity-main/paymentsVariants1.png'
import paymentsVariants2 from '../../img/legal-entity-main/paymentsVariants2.png'
import legalEntityTopImg from '../../img/legal-entity-main/legalEntityTopImg.png'
import legalEntityTopImgNarrow from '../../img/legal-entity-main/legalEntityTopImg2.png'

import InfoBlock from '../../components/Blocks/InfoBlock/InfoBlock'
import TopBlockWithImageNEW from '../../components/Blocks/TopBlockWithImageNEW/TopBlockWithImageNEW'
import ColouredBGBlock from '../../components/Blocks/ColouredBGBlock/ColouredBGBlock'
import GrayCardWithImg from '../../components/Cards/GrayCardWithImg/GrayCardWithImg'



const LegalEntity = ({history}) => {

    

    return (
        <div className={styles.LegalEntity}>

            <TopBlockWithImageNEW
                topBlockImg={legalEntityTopImg}
                topBlockImgNarrow={legalEntityTopImgNarrow}
                topBlockItemText1={'VEPAY:       '}
                topBlockItemText2={' .  .  .    QR-.  . -.    VEPAY!'}
                centeredBlockStyle={styles.centeredBlockStyle}
            >
                <div className={styles.supportBlock}>
                    <div>
                        <span>24/7</span> 
                        <br/>
                        <p> </p>
                        
                    </div>
                    <div>
                        <span>3 </span> 
                        <br/>
                        <p> </p>
                        
                    </div>
                    <div>
                        <span>98%</span> 
                        <br/>
                        <p> </p>
                        
                    </div>
                </div>
            </TopBlockWithImageNEW>

            <ColouredBGBlock>
                <div className={styles.receivingPaymentsBlock}>
                    <h1>   VEPAY</h1>
                    <div className={styles.payVariantsBlock}>
                        <div className={styles.payVariantsItem}>
                            <div className={styles.itemImgWrap}>
                                <img src={paymentsVariants1} alt="paymentsVariants1"/>
                            </div>
                            <div className={styles.variant}>
                                <h2>  </h2>
                                <p>
                                     VEPAY,   :         -.
                                </p>
                            </div>
                            <NavLink to='/receiving-payments' className={styles.detailsLink}>
                                
                            </NavLink>
                        </div>
                        <div className={styles.payVariantsItem}>
                            <div className={styles.itemImgWrap}>
                                <img src={paymentsVariants2} alt="paymentsVariants2"/>
                            </div>
                            <div className={styles.variant}>
                                <h2>    </h2>
                                <p>
                                      ,   .
                                </p>
                            </div>
                            <NavLink to='/receiving-payments'  className={styles.detailsLink}>
                                
                            </NavLink>
                        </div>
                    </div>
                    
                </div>

                <InfoBlock
                    history={history}
                />

                <div className={styles.productsBlock}>
                    <div className={styles.header}>
                        <h1></h1>
                        <h2>       .</h2>
                    </div>
                    <div className={styles.products}>
                        <GrayCardWithImg
                            clickHander={() => history.push("/payouts")}
                            image={productImg1}
                            header={''}
                            text={'            .'}
                        />
                        <GrayCardWithImg
                            bodyClass={styles.productsItem}
                            clickHander={() => history.push("/54-fz")}
                            image={productImg2}
                            header={'  54-'}
                            text={'       54-    .'}
                        />
                        <GrayCardWithImg
                            
                            clickHander={() => history.push("/credits")}
                            image={productImg3}
                            header={'   '}
                            text={'      .      .'}
                        />
                        <GrayCardWithImg
                            bodyClass={styles.productsItem}
                            clickHander={() => history.push("/developers/personal-account")}
                            image={productImg4}
                            header={' '}
                            text={' ,  ,  '}
                        />
                    </div>

                </div>

            </ColouredBGBlock>
           

            {/* 

           

            <SkewedBlock >
                
            </SkewedBlock>

            <div className={styles.partnerProgramme}>
                <div className={styles.partnerProgrammeBlock}>
                    <div className={styles.partnerProgrammeBlockBtn}>
                        <Button
                            type='green'
                            text='  '
                            clickHander={() => history.push("/partners")}
                        />
                    </div>

                    <div className={styles.partnerProgrammeHalfCircle}>
                        <div className={styles.HalfCircleContent}>
                            <h1>  </h1>
                            <p>
                                  -  .   !
                        </p>
                        </div>

                    </div>
                </div>
            </div> */}

        </div>
    )
}

export default LegalEntity

