import React, { useState } from 'react'

import styles from './Tariffs.module.scss'
import topImg from '../../img/tariffs/tariffsTopImg.png'
import topImg2 from '../../img/tariffs/tariffsTopImg2.png'
import internetShopImg from '../../img/tariffs/tariffIntShop.png'
import aviaImg from '../../img/tariffs/tariffAvia.png'
import housingImg from '../../img/tariffs/tariffHousing.png'
import CardWithButton from '../../components/Cards/CardWithButton/CardWithButton'
import TopBlockWithImageNEW from '../../components/Blocks/TopBlockWithImageNEW/TopBlockWithImageNEW'
import ColouredBGBlock from '../../components/Blocks/ColouredBGBlock/ColouredBGBlock'

const Tariffs = (props) => {

    const [intShopValue, setIntShopValue] = useState(' 1 P')
    const [aviaValue, setAviaValue] = useState(' 1 P')
    const [housingValue, setHousingValue] = useState(' 1 P')

    const intShopPercent = {
        " 1 P": '2,7 %',
        " 1   5 ": '2,6 %',
        " 5   15 ": '2,5 %',
        " 15   30 ": '2,3 %',
        " 30   ": ' '
    }

    const aviaPercent = {
        " 1 P": '1,7 %',
        " 1   5 ": '1,6 %',
        " 5   15 ": '1,4 %',
        " 15   30 ": '1,2 %',
        " 30   ": ' '
    }

    const housingPercent = {
        " 1 P": '1,5 %',
        " 1   5 ": '1,3 %',
        " 5   15 ": '1,1 %',
        " 15   30 ": '0,9 %',
        " 30   ": ' '
    }

    return (
        <div className={styles.Tariffs}>
            
           <TopBlockWithImageNEW
                topBlockImg={topImg}
                topBlockImgNarrow={topImg2}
                topBlockItemText1={''}
                topBlockItemText2={' VEPAY        ,    .'}
           />

            <ColouredBGBlock>
                <div className={styles.tariffsBlock}>
                    <h1 className={styles.tariffsHeader}>  VEPAY</h1>
                    <CardWithButton  header='-' image={internetShopImg}
                    >
                        <p className={styles.cardText1}>  </p>
                        <select 
                            value={intShopValue} 
                            name="internet-shops" 
                            id="internet-shops"
                            onChange={(e) => setIntShopValue(e.target.value)}
                        >
                            <option value=" 1 P"> 1 P</option>
                            <option value=" 1   5 "> 1   5 </option>
                            <option value=" 5   15 "> 5   15 </option>
                            <option value=" 15   30 "> 15   30 </option>
                            <option value=" 30   "> 30   </option>
                        </select>
                        <p>
                            <small>  VEPAY</small>
                        </p>
                        <h1>{intShopPercent[intShopValue]}</h1>
                        
                    </CardWithButton>
                    <CardWithButton header=''  image={aviaImg}>
                        <p className={styles.cardText1}>  </p>
                        <select 
                            value={aviaValue} 
                            name="avia" 
                            id="avia"
                            onChange={(e) => setAviaValue(e.target.value)}
                        >
                            <option value=" 1 P"> 1 P</option>
                            <option value=" 1   5 "> 1   5 </option>
                            <option value=" 5   15 "> 5   15 </option>
                            <option value=" 15   30 "> 15   30 </option>
                            <option value=" 30   "> 30   </option>
                        </select>
                        <p>
                            <small>  VEPAY</small>
                        </p>
                        <h1>{aviaPercent[aviaValue]}</h1>
                        
                    </CardWithButton>
                    <CardWithButton header=''  image={housingImg}>
                        <p className={styles.cardText1}>  </p>
                        <select 
                            value={housingValue} 
                            name="housing" 
                            id="housing"
                            onChange={(e) => setHousingValue(e.target.value)}
                        >
                            <option value=" 1 P"> 1 P</option>
                            <option value=" 1   5 "> 1   5 </option>
                            <option value=" 5   15 "> 5   15 </option>
                            <option value=" 15   30 "> 15   30 </option>
                            <option value=" 30   "> 30   </option>
                        </select>
                        <p>
                            <small>  VEPAY</small>
                        </p>
                        <h1>{housingPercent[housingValue]}</h1>
                        
                    </CardWithButton>
                </div>

                <div className={styles.bottomTextBlock}>
                    <p className={styles.bottomText}>
                              ,      .
                    </p>
                 </div>
            </ColouredBGBlock>

            

        </div>
    )
}

export default Tariffs