import AdaptiveImg from '../../../components/AdaptiveImg/AdaptiveImg'

const ApplePay = (props) => {

    return (
        <div className={styles.ApplePay}>
           <AdaptiveDevBlock>
            <h1>Apple Pay</h1>
            <p>
            VEPAY  Apple Pay           iOS   .        Touch ID  Face ID.      ,     Apple.    Apple Pay      .
            </p>
            <p>
                  Apple Pay       Apple Merchant ID, /    ,     Apple  ,     .       apple.com .
            </p>
            <p>
                Apple Pay   VEPAY  .
            </p>
            <AdaptiveImg image={img1} />







           </AdaptiveDevBlock>
        </div>
    )
}

export default ApplePay